.slider-preview-settings {
    display: flex;
    flex-wrap: wrap;
    padding: 24px;
    margin: 0 -8px;

    .input-item {
        padding: 0 8px;
        margin-bottom: 16px;
        flex: 1;
        label {
            display: block;
        }
        input {
            &[type="text"],
            &[type="number"] {
                height: 42px;
                border: solid 1px #eee;
                padding: 0 12px;
                width: 80%;
            }
            &[type="checkbox"],
            &[type="radio"] {
                border: solid 1px #eee;
                height: 18px;
                width: 18px;
            }
        }
    }
}

